@import "_variables";

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  margin: 0;
  // Provide a default background for when some browsers "scroll off the page"
  // as part of their animation when the top or bottom is reached.
  background-color: black;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: $nav-width;

  @include setup(
    $backgroundColor: map-get($colors, "black"),
    $backgroundImage: "/images/tile_blk.gif",
    $linkActiveColor: map-get($colors, "electricViolet1"),
    $linkColor: map-get($colors, "brightRed2"),
    $linkVisitedColor: map-get($colors, "blazeOrange"),
  );
}

.root {
  margin-left: $nav-width;
  flex-grow: 1;

  .header {
    position: fixed;
    top: 0;
    right: 0;
    left: $nav-width;
    height: $header-height;
    padding: 4px;
  }

  .main {
    min-height: 100%;
    padding: $main-padding;
    padding-top: $header-height + $main-padding;
  }
}
